body {
  margin: 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  background-color:white;
}
/* Nav ##########################*/
nav {
  position: sticky;
  top: 0;
  width: 100%;
  height: 64px;
  background-color: rgb(95, 170, 82);
  text-align: center;
  color: white;
  z-index: 2;
}
nav h4 {
  float: left;
  margin-left: 20px;
  cursor: pointer;
}
nav h6 {
  float: right;
  margin-right: 20px;
  cursor: pointer;
  font-size: 14px;
  margin-top: 26px;
}
nav input {
  margin-top: 18px;
  padding: 8px;
  border: none;
  border-radius: 8px;
  width: 70px;
  cursor: pointer;
  transition: all 0.35s ease-in-out;
  float: left;
  margin-left: 40px;
  background-image: url(/assest/NavIcon/search.png);
  background-position: 5px 5px;
  background-repeat: no-repeat;
  background-size: 20px 20px;
  padding-left: 30px;
}

#basket {
  fill: white;
  float: right;
  margin: 20px;
  margin-left: 5px;
  cursor: pointer;
}
::placeholder {
  font-style: italic;
}

nav input:hover {
  width: 150px;
}

nav input:focus {
  transform: scale(1.04);
}

/*ads####################################*/
#ads {
  width: 100%;
  height: 50px;
  background-color: rgb(68, 102, 117);
}
.adsTextElement {
  position: relative;
  display: flow-root;
  color: white;
  margin: 0;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
/* slider ############### */
#sliderContainer {
  overflow: hidden;
  white-space: nowrap;
  margin: auto;
  position: relative;
  user-select: none;
  background-color: gray;
  margin-bottom: 15px;
}
.sliderElement {
  width: 100%;
  height: 300px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: inline-block;
  transition: all 1s ease-in-out;
  padding: 0;

  /* margin-bottom: 5%; */
  background-color: gray;
}

#sliderContainer:hover #leftArrow {
  opacity: 1;
}
#sliderContainer:hover #rightArrow {
  opacity: 1;
}
.arrow {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  opacity: 0;
  fill: white;
  stroke: green;
  stroke-width: 0.4px;
  cursor: pointer;
  transition: all 0.5s;
}
#leftArrow {
  left: 25px;
}
#rightArrow {
  right: 25px;
}
#dots {
  opacity: 0;
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translate(-50%);
  transition: all 0.5s;
}
#dots svg {
  fill: green;
  cursor: pointer;
}
#sliderContainer:hover #dots {
  opacity: 1;
}
/* EmpfehlungenContainer */
.EmpfehlungenContainer {
  position: relative;
  width: 100%;
  height: 290px;
  stroke: gray;
  stroke-width: 1px;
  border: solid 1px white;
  white-space: nowrap;
  overflow: hidden;
  margin-bottom: 13px;
  padding-bottom: 1px;
}
.EmpfehlungenContainer:hover .productArrow {
  opacity: 1;
}
.productArrow {
  position: absolute;
  height: 100%;
  background: rgb(255, 255, 255, 0.8);
  opacity: 0;
  transition: opacity 0.5s;
}
.productLeftArrow {
  left: 0;
}
.productRightArrow {
  right: 0;
}
.EmpfehlungenContainer .productContainer {
  display: inline-block;
}
.EmpfehlungenContainerTitle {
  margin-top: 3px;
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 5px;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
    background-color: lavender;
    padding: 12px;
    border-radius: 10px;
    font-size: large;
    text-align: center;
}

/*productContainer*/

.productContainer {
  width: 170px;
  height: 230px;
  background-color: white;
  margin: 10px;
  padding-bottom: 5px;
}

.productContainer * {
  margin: 2px;
}
.productPic {
  width: 100%;
  height: 130px;
  margin: 0;
  background-size: cover;
}
.action h4 {
  color: red;
  margin: 2px;
  font-size: 12px;
}
#productRating {
  margin-left: 10px;
}
#ratespan {
  margin-left: 10px;
  margin-top: 10px;
  font-size: 8px;
}
#productName {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  margin-left: 10px;
  font-size: large;
  word-wrap: break-word;
  overflow: hidden;
}

#productPrice {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  margin-left: 10px;
  font-size: medium;
  margin-right: 30px;
}
#productNote {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  margin-left: 10px;
  font-size: medium;
  margin-bottom: 10px;
  overflow: hidden;
}
.ProductPricing {
  white-space: nowrap;
}
.ProductPricing h5 {
  display: inline;
}
#productOldPrice {
  text-decoration: line-through;
  color: red;
}

/* SideNavBar *************************************/
#SideNavBarContainer {
  position: fixed;
  left: 0;
  top: 0;
  width: 300px;
  height: 100vh;
  background-color: teal;
  transform-origin: left;
  transition: transform .5s;
}

#hamButton {
  float: left;
  margin-top: 18px;
  margin-left: 10px;
  fill: white;
  cursor: pointer;
}